import { StatusBar } from 'expo-status-bar';
import { useEffect, useState } from 'react';
import { StyleSheet, Text, View, ScrollView, Platform } from 'react-native';
import { log, fetchUrl, options, defUrl } from './Common';
import { useStore } from './store';
import Card from './components/Card';
import { createClient } from 'pexels';
import { TouchableOpacity } from 'react-native-web';
import TabBar from './components/TabBar';

export default function App() {
  const [word, setWord] = useState([])
  const [meaning, setMeaning] = useState([])
  const [source, setSource] = useState([])
  // const [q, setQ] = useState([])
  const data = useStore(state => state.en)
  const update = useStore(state => state.update)
  const client = createClient('563492ad6f917000010000010eea3c782765431797c7ea8b8176fb5e');
  const countTrue = useStore(state => state.countTrue)
  const count = useStore(state => state.countWord)

  const FetchWords = async () => {

    for (let i = 0; i < 10; i++) {
      fetch(fetchUrl, options)
        .then(response => response.json())
        .then(response => {
          // console.log(response)
          update(response)
        })
        .catch(err => {
          log(err)
        })
    }
  }

  const FindImages = async () => {
    for (let i = 0; i < 10; i++) {
      let query = q[i]
      const res = await client.photos.search({ query, per_page: 1 })
      const photos = res.photos
      const src = photos.map(photo => photo.src)
      const medium = src.map(s => s.medium)
      medium && setSource(medium)
      // log(medium)
    }
  }

  const Complete = () => {
   // log('Complete');
    log(countTrue)
    log(count)
    if(countTrue == count){
      log('Done')
    }
    else{
      log('Failed to complete')
      if(Platform.OS === 'web')
      alert('Failed to complete')
    }
  }

  useEffect(() => {
    FetchWords();
    // FindImages()
    // getImages()
  }, [])

  useEffect(() => {
    setWord(data.map(item => item.word))
    // setQ(word)
    // log(data)
  }, [data])

  return (
    <ScrollView style={styles.container}
      stickyHeaderIndices={[0]}>
      <TabBar />
      <View style={styles.cards}>
        {
          data.map((item) => item.results &&
            <Card
              word={item.word} key={item.word}
              meaning={item.results.map(def => def.definition)}
              typeOf={item.results.map(def => def.partOfSpeech)}
              prounciation={item.pronunciation ?
                (item.pronunciation.all ? ("\n/" + item.pronunciation.all + "/") :
                  ("\n/" + item.pronunciation + "/"))
                : " "}
              src={source[0]}
            />
          )
        }
      </View>
      <View style={styles.buttonView}>
        <TouchableOpacity style={styles.button}
          onPress={() => Complete()}>
          <Text style={styles.buttonText}>Complete</Text>
        </TouchableOpacity>
      </View>
      <StatusBar style="auto" />
    </ScrollView >
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',

  },
  cards: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'space-evenly',
    alignItems: 'center',
  },
  buttonView: {
    flexDirection: 'row',
    justifyContent: 'center',
    margin: 20
  },
  button: {
    width: 300,
    backgroundColor: "blue",
    padding: 10
  },
  buttonText: {
    textAlign: 'center',
    color: '#fff',
    fontWeight: 'bold',
  }
});
