import React, { useState, useEffect } from 'react';
import {
  Text,
  TextInput,
  View,
  StyleSheet,
  Button,
  Image,
  Alert,
  Platform
} from 'react-native';
import Icon from 'react-native-vector-icons/AntDesign';
import { WrongAlert } from './RNAlert';
//  --legacy-peer-deps
import { log } from '../Common'
import { useStore } from '../store';
import * as Speech from 'expo-speech';

export default function Card(props) {
  const [sound, setSound] = useState();
  const [playing, setPlaying] = useState(false);
  const [iconName, setIconName] = useState('play');
  const [checking, setChecking] = useState(false);
  const [text, setText] = useState('');
  const [en, setEn] = useState([])
  const data = useStore(state => state.en)
  const count = useStore(state => state.countWord)
  const incrementWord = useStore(state => state.incrementWord)
  const initCountWord = useStore(state => state.initCountWord)
  const countTrue = useStore(state => state.countTrue)
  const updateTrue = useStore(state => state.updateTrue)
  const [countTrueW, setCountTrueW] = useState(0)

  useEffect(() => {
    // log(Platform)
    initCountWord()
    let m = data.map(item => item.results);
    for (let i = 0; i < m.length; i++) {
      if (m[i] !== undefined) {
        // log(count)
        incrementWord()
      }
    }
  }, [])

  useEffect(() => {
    return sound
      ? () => {
        console.log('Unloading Sound');
        sound.unloadAsync();
      }
      : undefined;
  }, [sound]);

  const test = () => {
    setChecking(!checking);
  }

  const checkWord = () => {
    log(text)
    log(props.word)
    
    if (text == props.word) {
      setChecking(false);
      setCountTrueW(countTrueW + 1)
    }
    else {
      setChecking(true);
      if(countTrueW > 0) {
        setCountTrueW(countTrueW - 1)
      }
      else {
        setCountTrueW(0)
      }
      
      Platform.OS == 'web' ? alert("Wrong answer!") :
      Alert.alert(
        "Wrong answer",
        [
          {
            text: "Cancel",
            onPress: () => console.log("Cancel Pressed"),
            style: "cancel"
          },
          { text: "OK", onPress: () => console.log("OK Pressed") }
        ]
      )
    }
  }

  const speak = (w) => {
    if (playing) {
      console.log('Loading Sound');
      setIconName('pausecircle');
      Speech.speak(w);
      setTimeout(() => { setIconName('play') }, 1100)
    } else {
      setIconName('play');
      // await sound.unloadAsync();
    }
  };

  useEffect(() =>
    {
       // log(countTrueW)
       updateTrue(countTrueW)
       // log(countTrue)
    }
   , [countTrueW])

  return (
    <View style={styles.card}>
      <View style={styles.cardHeader}>
        {
          checking ? (
            <>
              <TextInput
                style={styles.inputText}
                placeholder=""
                onChangeText={text => setText(text)}
              />
              <Button title="Check"
                style={styles.button}
                onPress={() => checkWord()} />
            </>) : (
            <Text style={[
              styles.text,
              styles.textHeader
            ]}>
              {props.word} ({props.typeOf == "noun"
                ? "n" : props.typeOf == "adjective"
                  ? "adj" : props.typeOf == "verb"
                    ? "v" : props.typeOf[0] + ", " + props.typeOf[1]})
              {props.prounciation}
            </Text>)
        }

        <Icon
          name={iconName}
          size={20} color="#000"
          style={styles.icon}
          onPress={() => {
            setPlaying(true);
            speak(props.word)
          }} />
      </View>
      <Text
        style={styles.checkText}
        onPress={() => { test() }}>
         {!checking ?
          "Check this word"
          : "View"}
      </Text>
      <Image style={styles.img}
        source={{ uri: props.src }} />
      <Text style={[
        styles.text,
        styles.textBody
      ]}>
        {props.example}
      </Text>
      <Text style={styles.text}>
        {props.meaning}
      </Text>
    </View>
  );
}

const styles = StyleSheet.create({
  card: {
    width: 300,
    backgroundColor: '#f0f2f5',
    padding: 10,
    margin: 10,
    borderRadius: 7,
  },
  cardHeader: {
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  inputText: {
    height: 30,
    margin: 12,
    borderWidth: 1,
    padding: 10
  },
  button: {
    borderWidth: 1,
    padding: 4
  },
  textHeader: {
    marginRight: 10,
  },
  textBody: {
    textAlign: 'justify',
    fontSize: 18,
  },
  checkText: {
    textAlign: 'center',
    marginTop: 5,
    cursor: 'pointer'
  },
  text: {
    fontSize: 20,
    textAlign: 'center',
  },
  icon: {
    marginTop: 5,
    alignItems: 'center',
  },
  img: {
    width: 300,
    height: 300,
  }
});

Card.defaultProps = {
  src: "https://www.seekpng.com/png/detail/968-9681864_apple-png-background-stock-photo-apple-fruit.png"
}
