import create from 'zustand'

export const useStore = create(set => ({
  countWord: 0,
  initCountWord: () => set(state => ({ countWord: 0 })),
  incrementWord: () => set(state => ({ countWord: state.countWord + 1 })),
  countTrue: 0,
  updateTrue: (i) => set(state => ({ countTrue: state.countTrue + i })),
  en: [],
  update: (data) => set(state => ({ en: [...state.en, data] })),
  meaning: [],
  updateMeaning: (data) => set(state => ({ meaning: [...state.meaning, data] }))
}))